import axios from 'axios'
import qs from 'qs'
import {Message} from 'element-ui'

// let baseURL = process.env.NODE_ENV == 'development'
//   ?
//   ($URL.JSON ? process.env.BASE_API : $URL.DATA_API)
//   :
//   ($URL.JSON ? $URL.BASE_API : $URL.DATA_API);
let baseURL =$URL.JSON ? './':$URL.DATA_API;
  
  console.log('baseURL:'+baseURL);
const service = axios.create({
  baseURL: baseURL, // api 的base_url
  // timeout: 6000 // request timeout，当网络很慢，测试的时候，可以屏蔽该延时
})

// console.log("部署地址：" + $URL.BASE_API);
console.log("接口地址：" + baseURL);

service.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

//http request 拦截器（对发送的数据做提前处理）
service.interceptors.request.use(
  config => {
    // 在发送请求之前做些什么
    /**
     * var a = {name:'hehe',age:10};
     * qs.stringify(a)
     * => 'name=hehe&age=10'
     */

    // Vue.$loading.show();

    if (config.method == "post") {
      config.data = qs.stringify(config.data);
    }

    return config;
  },
  err => {
    return Promise.reject(err);
  }
);

//http response 拦截器（对返回的数据做一些处理）
service.interceptors.response.use(
  res => {
    // Vue.$loading.hide();
    return res;
  },
  err => {
    // Vue.$loading.hide();
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = '错误请求'
          break;
        case 401:
          err.message = '未授权，请重新登录'
          break;
        case 403:
          err.message = '403拒绝访问'
          break;
        case 404:
          err.message = '404错误,未找到该资源'
          break;
        case 405:
          err.message = '请求方法未允许'
          break;
        case 408:
          err.message = '请求超时'
          break;
        case 500:
          err.message = '服务器端出错'
          break;
        case 501:
          err.message = '网络未实现'
          break;
        case 502:
          err.message = '网络错误'
          break;
        case 503:
          err.message = '503服务不可用'
          break;
        case 504:
          err.message = '网络超时'
          break;
        case 505:
          err.message = 'http版本不支持该请求'
          break;
        default:
          err.message = `连接错误${err.response.status}`
      }
    } else {
      err.message = "连接到服务器失败"
    }
    console.log('err' + err) // for debug
    Message({
      message: err.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(err)
  }
)

export default service
